<template>
	<view class="container">
		<!-- ÊâìÂç∞Á±ªÂûãÂàáÊç¢ -->
		<view class="type-tabs">
			<view 
				v-for="type in printTypes" 
				:key="type.value"
				class="tab-item"
				:class="{'active': currentType === type.value}"
				@click="switchType(type.value)"
			>
				<text>{{ type.label }}</text>
			</view>
		</view>
		
		<!-- ÊñáÊú¨ÊâìÂç∞ -->
		<view class="print-content" v-if="currentType === 'text'">
			<view class="form-item">
				<text class="label">ËæìÂÖ•ÊñáÊú¨ÂÜÖÂÆπ</text>
				<textarea 
					v-model="textContent" 
					class="textarea"
					placeholder="ËØ∑ËæìÂÖ•Ë¶ÅÊâìÂç∞ÁöÑÊñáÊú¨ÂÜÖÂÆπ"
					maxlength="2000"
				></textarea>
				<text class="count">{{ textContent.length }}/2000</text>
			</view>
			
			<view class="form-item">
				<text class="label">Â≠ó‰ΩìÂ§ßÂ∞è</text>
				<slider 
					:value="fontSize" 
					@change="fontSizeChange"
					min="12" 
					max="48" 
					step="2"
					show-value
				></slider>
			</view>
		</view>
		
		<!-- ÂõæÁâáÊâìÂç∞ -->
		<view class="print-content" v-if="currentType === 'image'">
			<view class="image-grid">
				<view 
					v-for="(img, index) in imageList" 
					:key="index"
					class="image-item"
				>
					<image :src="img" mode="aspectFill"></image>
					<view class="delete-icon" @click="deleteImage(index)">√ó</view>
				</view>
				<view class="upload-btn" @click="chooseImage" v-if="imageList.length < 9">
					<text>+</text>
					<text class="upload-text">Ê∑ªÂä†ÂõæÁâá</text>
				</view>
			</view>
		</view>
		
		<!-- ÊñáÊ°£ÊâìÂç∞ -->
		<view class="print-content" v-if="currentType === 'document'">
			<view class="form-item">
				<text class="label">ËæìÂÖ•ÊñπÂºè</text>
				<view class="input-type-btns">
					<button 
						class="input-type-btn"
						:class="{'active': inputType === 'file'}"
						@click="inputType = 'file'"
					>
						Êú¨Âú∞Êñá‰ª∂
					</button>
					<button 
						class="input-type-btn"
						:class="{'active': inputType === 'url'}"
						@click="inputType = 'url'"
					>
						Êñá‰ª∂URL
					</button>
				</view>
			</view>
			
			<!-- Êú¨Âú∞Êñá‰ª∂ -->
			<view v-if="inputType === 'file'">
				<view class="file-list">
					<view v-for="(file, index) in fileList" :key="index" class="file-item">
						<view class="file-info">
							<text class="file-icon">üìÑ</text>
							<view class="file-detail">
								<text class="file-name">{{ file.name }}</text>
								<text class="file-size">{{ formatFileSize(file.size) }}</text>
							</view>
						</view>
						<text class="delete-text" @click="deleteFile(index)">Âà†Èô§</text>
					</view>
					<view class="empty-tip" v-if="fileList.length === 0">
						<text>ÊöÇÊó†Êñá‰ª∂</text>
					</view>
				</view>
				<button class="upload-file-btn" @click="chooseFile">ÈÄâÊã©Êñá‰ª∂</button>
				<view class="tip-text">
					<text>ÊîØÊåÅ PDF„ÄÅWord„ÄÅExcel„ÄÅPPT Á≠âÊ†ºÂºè</text>
				</view>
			</view>
			
			<!-- URLËæìÂÖ• -->
			<view v-if="inputType === 'url'">
				<view class="form-item">
					<text class="label">Êñá‰ª∂URL</text>
					<textarea 
						v-model="fileUrl" 
						class="textarea"
						placeholder="ËØ∑ËæìÂÖ•Êñá‰ª∂ÁöÑÂÆåÊï¥URLÂú∞ÂùÄÔºå‰æãÂ¶ÇÔºöhttps://example.com/document.pdf"
						auto-height
					></textarea>
				</view>
				<view class="tip-text">
					<text>üí° ËØ∑Á°Æ‰øùURLÂèØÂÖ¨ÂºÄËÆøÈóÆÔºåÊîØÊåÅ PDF„ÄÅOffice ÊñáÊ°£Á≠â</text>
				</view>
			</view>
		</view>
		
		<!-- Ê†áÁ≠æÊâìÂç∞ -->
		<view class="print-content" v-if="currentType === 'label'">
			<view class="form-item">
				<text class="label">Ê†áÁ≠æÂÜÖÂÆπ</text>
				<input 
					v-model="labelContent" 
					class="input"
					placeholder="ËØ∑ËæìÂÖ•Ê†áÁ≠æÂÜÖÂÆπ"
				/>
			</view>
			<view class="form-item">
				<text class="label">Ê†áÁ≠æÊ®°Êùø</text>
				<picker :range="labelTemplates" range-key="name" @change="templateChange">
					<view class="picker">
						<text>{{ selectedTemplate.name || 'ËØ∑ÈÄâÊã©Ê®°Êùø' }}</text>
					</view>
				</picker>
			</view>
		</view>
		
		<!-- ÊâìÂç∞ËÆæÁΩÆ -->
		<view class="settings-section">
			<view class="section-title">ÊâìÂç∞ËÆæÁΩÆ</view>
			
			<view class="setting-item">
				<text class="setting-label">ÊâìÂç∞Êú∫</text>
				<view class="setting-value" @click="selectPrinter">
					<text>{{ currentPrinter ? currentPrinter.name : 'ËØ∑ÈÄâÊã©ÊâìÂç∞Êú∫' }}</text>
					<text class="arrow">></text>
				</view>
			</view>
			
			<view class="setting-item">
				<text class="setting-label">ÊâìÂç∞‰ªΩÊï∞</text>
				<view class="counter">
					<button class="counter-btn" @click="changeCopies(-1)">-</button>
					<text class="counter-value">{{ copies }}</text>
					<button class="counter-btn" @click="changeCopies(1)">+</button>
				</view>
			</view>
			
			<view class="setting-item">
				<text class="setting-label">Á∫∏Âº†Â§ßÂ∞è</text>
				<picker :range="paperSizes" range-key="name" @change="paperSizeChange">
					<view class="picker">
						<text>{{ currentPaperSize.name }}</text>
					</view>
				</picker>
			</view>
			
			<view class="setting-item">
				<text class="setting-label">ÊâìÂç∞ÊñπÂêë</text>
				<view class="orientation-btns">
					<button 
						class="orientation-btn"
						:class="{'active': orientation === 1}"
						@click="orientation = 1"
					>
						Á´ñÂêë
					</button>
					<button 
						class="orientation-btn"
						:class="{'active': orientation === 2}"
						@click="orientation = 2"
					>
						Ê®™Âêë
					</button>
				</view>
			</view>
							
			<view class="setting-item">
				<text class="setting-label">È¢úËâ≤</text>
				<view class="color-btns">
					<button 
						class="color-btn"
						:class="{'active': colorMode === 2}"
						@click="colorMode = 2"
					>
						ÂΩ©Ëâ≤
					</button>
					<button 
						class="color-btn"
						:class="{'active': colorMode === 1}"
						@click="colorMode = 1"
					>
						ÈªëÁôΩ
					</button>
				</view>
			</view>
							
			<view class="setting-item">
				<text class="setting-label">ÂèåÈù¢ÊâìÂç∞</text>
				<view class="duplex-btns">
					<button 
						class="duplex-btn"
						:class="{'active': duplexMode === 1}"
						@click="duplexMode = 1"
					>
						ÂÖ≥Èó≠
					</button>
					<button 
						class="duplex-btn"
						:class="{'active': duplexMode === 3}"
						@click="duplexMode = 3"
					>
						Áü≠Ëæπ
					</button>
					<button 
						class="duplex-btn"
						:class="{'active': duplexMode === 2}"
						@click="duplexMode = 2"
					>
						ÈïøËæπ
					</button>
				</view>
			</view>
		</view>
		
		<!-- ÊâìÂç∞ÊåâÈíÆ -->
		<view class="bottom-bar">
			<button class="preview-btn" @click="previewPrint">È¢ÑËßà</button>
			<button class="print-btn" @click="startPrint">ÂºÄÂßãÊâìÂç∞</button>
		</view>
	</view>
</template>

<script>
	import printApi from '@/utils/printApi.js';
	
	export default {
		data() {
			return {
				// ÊâìÂç∞Á±ªÂûã
				printTypes: [
					{ label: 'ÊñáÊú¨', value: 'text' },
					{ label: 'ÂõæÁâá', value: 'image' },
					{ label: 'ÊñáÊ°£', value: 'document' },
					{ label: 'Ê†áÁ≠æ', value: 'label' }
				],
				currentType: 'text',
							
				// ÊñáÊú¨ÊâìÂç∞
				textContent: '',
				fontSize: 14,
							
				// ÂõæÁâáÊâìÂç∞
				imageList: [],
							
				// ÊñáÊ°£ÊâìÂç∞
				inputType: 'url', // 'file' Êàñ 'url'
				fileList: [],
				fileUrl: '',
							
				// Ê†áÁ≠æÊâìÂç∞
				labelContent: '',
				labelTemplates: [
					{ name: 'Ê†áÂáÜÊ†áÁ≠æ', value: 'standard' },
					{ name: 'Âø´ÈÄíÊ†áÁ≠æ', value: 'express' },
					{ name: '‰ª∑Ê†ºÊ†áÁ≠æ', value: 'price' }
				],
				selectedTemplate: {},
							
				// ÊâìÂç∞ËÆæÁΩÆ
				currentPrinter: null,
				copies: 1,
				paperSizes: [
					{ name: 'A4', value: 9 },
					{ name: 'A5', value: 11 },
					{ name: 'Letter', value: 1 },
					{ name: 'A3', value: 8 }
				],
				currentPaperSize: { name: 'A4', value: 9 },
				orientation: 1, // 1=Á´ñÂêë, 2=Ê®™Âêë
				colorMode: 1, // 1=ÈªëÁôΩ, 2=ÂΩ©Ëâ≤
				duplexMode: 1 // 1=ÂÖ≥Èó≠ÂèåÈù¢, 2=ÈïøËæπ, 3=Áü≠Ëæπ
			}
		},
		
		onLoad(options) {
			if (options.type) {
				this.currentType = options.type;
			}
			this.loadPrinter();
		},
		
		methods: {
			// ÂàáÊç¢ÊâìÂç∞Á±ªÂûã
			switchType(type) {
				this.currentType = type;
			},
			
			// Âä†ËΩΩÊâìÂç∞Êú∫
			loadPrinter() {
				this.currentPrinter = uni.getStorageSync('selectedPrinter');
			},
			
			// ÈÄâÊã©ÊâìÂç∞Êú∫
			selectPrinter() {
				uni.navigateTo({
					url: '/pages/printer/printer'
				});
			},
			
			// Â≠ó‰ΩìÂ§ßÂ∞èÂèòÂåñ
			fontSizeChange(e) {
				this.fontSize = e.detail.value;
			},
			
			// ÈÄâÊã©ÂõæÁâá
			chooseImage() {
				uni.chooseImage({
					count: 9 - this.imageList.length,
					sizeType: ['original', 'compressed'],
					sourceType: ['album', 'camera'],
					success: (res) => {
						this.imageList = this.imageList.concat(res.tempFilePaths);
					}
				});
			},
			
			// Âà†Èô§ÂõæÁâá
			deleteImage(index) {
				this.imageList.splice(index, 1);
			},
			
			// ÈÄâÊã©Êñá‰ª∂
			chooseFile() {
				// ÂæÆ‰ø°Â∞èÁ®ãÂ∫èÈÄâÊã©Êñá‰ª∂
				uni.chooseMessageFile({
					count: 10,
					type: 'file',
					success: (res) => {
						this.fileList = this.fileList.concat(res.tempFiles.map(file => ({
							name: file.name,
							path: file.path,
							size: file.size
						})));
					}
				});
			},
			
			// Âà†Èô§Êñá‰ª∂
			deleteFile(index) {
				this.fileList.splice(index, 1);
			},
			
			// Ê†ºÂºèÂåñÊñá‰ª∂Â§ßÂ∞è
			formatFileSize(size) {
				if (size < 1024) {
					return size + 'B';
				} else if (size < 1024 * 1024) {
					return (size / 1024).toFixed(2) + 'KB';
				} else {
					return (size / 1024 / 1024).toFixed(2) + 'MB';
				}
			},
			
			// Ê®°ÊùøÂèòÂåñ
			templateChange(e) {
				this.selectedTemplate = this.labelTemplates[e.detail.value];
			},
			
			// Á∫∏Âº†Â§ßÂ∞èÂèòÂåñ
			paperSizeChange(e) {
				this.currentPaperSize = this.paperSizes[e.detail.value];
			},
			
			// ÊîπÂèò‰ªΩÊï∞
			changeCopies(delta) {
				const newCopies = this.copies + delta;
				if (newCopies >= 1 && newCopies <= 99) {
					this.copies = newCopies;
				}
			},
			
			// È¢ÑËßàÊâìÂç∞
			previewPrint() {
				if (!this.validatePrintData()) {
					return;
				}
				
				uni.showToast({
					title: 'È¢ÑËßàÂäüËÉΩÂºÄÂèë‰∏≠',
					icon: 'none'
				});
			},
			
			// È™åËØÅÊâìÂç∞Êï∞ÊçÆ
			validatePrintData() {
				if (!this.currentPrinter) {
					uni.showToast({
						title: 'ËØ∑ÂÖàÈÄâÊã©ÊâìÂç∞Êú∫',
						icon: 'none'
					});
					return false;
				}
								
				if (this.currentType === 'text' && !this.textContent) {
					uni.showToast({
						title: 'ËØ∑ËæìÂÖ•ÊñáÊú¨ÂÜÖÂÆπ',
						icon: 'none'
					});
					return false;
				}
								
				if (this.currentType === 'image' && this.imageList.length === 0) {
					uni.showToast({
						title: 'ËØ∑ÈÄâÊã©ÂõæÁâá',
						icon: 'none'
					});
					return false;
				}
								
				if (this.currentType === 'document') {
					if (this.inputType === 'file' && this.fileList.length === 0) {
						uni.showToast({
							title: 'ËØ∑ÈÄâÊã©Êñá‰ª∂',
							icon: 'none'
						});
						return false;
					}
					if (this.inputType === 'url' && !this.fileUrl) {
						uni.showToast({
							title: 'ËØ∑ËæìÂÖ•Êñá‰ª∂URL',
							icon: 'none'
						});
						return false;
					}
				}
								
				if (this.currentType === 'label' && !this.labelContent) {
					uni.showToast({
						title: 'ËØ∑ËæìÂÖ•Ê†áÁ≠æÂÜÖÂÆπ',
						icon: 'none'
					});
					return false;
				}
								
				return true;
			},
			
			// ÂºÄÂßãÊâìÂç∞
			async startPrint() {
				if (!this.validatePrintData()) {
					return;
				}
								
				uni.showLoading({
					title: 'Ê≠£Âú®ÊâìÂç∞...'
				});
								
				try {
					let result;
									
					// ÊûÑÂª∫ÈÄöÁî®ÊâìÂç∞ÂèÇÊï∞
					const printData = {
						deviceId: this.currentPrinter.id,
						devicePassword: this.currentPrinter.password,
						printerName: this.currentPrinter.model || this.currentPrinter.name,
						driverName: this.currentPrinter.driverName || this.currentPrinter.model,
						dmCopies: this.copies,
						dmPaperSize: this.currentPaperSize.value,
						dmOrientation: this.orientation,
						dmColor: this.colorMode,
						dmDuplex: this.duplexMode,
						isPreview: 1 // ÁîüÊàêÈ¢ÑËßàÂõæ
					};
									
					// Ê†πÊçÆÁ±ªÂûãË∞ÉÁî®‰∏çÂêåÁöÑÊâìÂç∞Êé•Âè£
					if (this.currentType === 'document') {
						if (this.inputType === 'url') {
							// ‰ΩøÁî®URLÊâìÂç∞
							result = await printApi.submitPrintTask({
								...printData,
								jobFileUrl: this.fileUrl
							});
						} else {
							// ÂÖà‰∏ä‰º†Êñá‰ª∂
							const fileUrls = [];
							for (let file of this.fileList) {
								const uploadResult = await printApi.uploadFile(file.path);
								fileUrls.push(uploadResult.data.url);
							}
							// ‰ΩøÁî®Á¨¨‰∏Ä‰∏™Êñá‰ª∂URL
							result = await printApi.submitPrintTask({
								...printData,
								jobFileUrl: fileUrls[0]
							});
						}
					} else if (this.currentType === 'image') {
						// ÂÖà‰∏ä‰º†ÂõæÁâá
						const imageUrls = [];
						for (let img of this.imageList) {
							const uploadResult = await printApi.uploadFile(img);
							imageUrls.push(uploadResult.data.url);
						}
						result = await printApi.printImage({
							...printData,
							imageUrls: imageUrls
						});
					} else if (this.currentType === 'label') {
						result = await printApi.printLabel({
							...printData,
							content: this.labelContent,
							template: this.selectedTemplate.value
						});
					} else {
						// ÊñáÊú¨ÊâìÂç∞ - ‰ΩøÁî®ÊóßAPI
						result = await printApi.printText({
							...printData,
							content: this.textContent,
							fontSize: this.fontSize
						});
					}
									
					// ‰øùÂ≠òÊâìÂç∞ÂéÜÂè≤
					this.savePrintHistory(result);
									
					uni.hideLoading();
									
					// ÊòæÁ§∫‰ªªÂä°ID
					const taskId = result.data?.task_id || result.data?.jobId;
					if (taskId) {
						uni.showModal({
							title: 'ÊâìÂç∞‰ªªÂä°Â∑≤Êèê‰∫§',
							content: `‰ªªÂä°ID: ${taskId}\n\nÂèØÂú®ÂéÜÂè≤ËÆ∞ÂΩï‰∏≠Êü•ËØ¢‰ªªÂä°Áä∂ÊÄÅ`,
							confirmText: 'Êü•ÁúãÂéÜÂè≤',
							cancelText: 'ÁªßÁª≠ÊâìÂç∞',
							success: (res) => {
								if (res.confirm) {
									uni.navigateTo({
										url: '/pages/history/history'
									});
								}
							}
						});
					} else {
						uni.showToast({
							title: 'ÊâìÂç∞ÊàêÂäü',
							icon: 'success'
						});
					}
									
					// Ê∏ÖÁ©∫Ë°®Âçï
					this.clearForm();
									
				} catch (error) {
					uni.hideLoading();
					console.error('ÊâìÂç∞Â§±Ë¥•:', error);
					uni.showModal({
						title: 'ÊâìÂç∞Â§±Ë¥•',
						content: error.message || error.msg || 'Êú™Áü•ÈîôËØØ',
						showCancel: false
					});
				}
			},
			
			// ‰øùÂ≠òÊâìÂç∞ÂéÜÂè≤
			savePrintHistory(result) {
				let history = uni.getStorageSync('printHistory') || [];
				history.unshift({
					id: result.data?.task_id || result.data?.jobId || Date.now(),
					type: this.currentType,
					printer: this.currentPrinter.name,
					time: new Date().toISOString(),
					status: 'pending',
					params: {
						copies: this.copies,
						paperSize: this.currentPaperSize.name,
						orientation: this.orientation === 1 ? 'Á´ñÂêë' : 'Ê®™Âêë',
						colorMode: this.colorMode === 1 ? 'ÈªëÁôΩ' : 'ÂΩ©Ëâ≤',
						duplexMode: this.duplexMode === 1 ? 'ÂÖ≥Èó≠' : (this.duplexMode === 2 ? 'ÈïøËæπ' : 'Áü≠Ëæπ')
					}
				});
								
				// Âè™‰øùÁïôÊúÄËøë100Êù°ËÆ∞ÂΩï
				if (history.length > 100) {
					history = history.slice(0, 100);
				}
								
				uni.setStorageSync('printHistory', history);
			},
			
			// Ê∏ÖÁ©∫Ë°®Âçï
			clearForm() {
				this.textContent = '';
				this.imageList = [];
				this.fileList = [];
				this.fileUrl = '';
				this.labelContent = '';
				this.copies = 1;
			}
		}
	}
</script>

<style scoped>
	.container {
		min-height: 100vh;
		background-color: #f5f5f5;
		padding-bottom: 140rpx;
	}
	
	/* Á±ªÂûãÊ†áÁ≠æ */
	.type-tabs {
		display: flex;
		background-color: #ffffff;
		padding: 20rpx;
	}
	
	.tab-item {
		flex: 1;
		text-align: center;
		padding: 20rpx 0;
		border-radius: 12rpx;
		font-size: 28rpx;
		color: #666666;
	}
	
	.tab-item.active {
		background-color: #1890ff;
		color: #ffffff;
	}
	
	/* ÊâìÂç∞ÂÜÖÂÆπ */
	.print-content {
